<template>
  <div>
    <fTitle>
      <template #title>Input 输入框</template>
      <template #introduce>通过鼠标或键盘输入字符</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
           默认框 <el-input v-model="input" placeholder="请输入内容"></el-input>
          </div></el-col>
          <el-col :span="8"> <div class="grid-content">
           禁用框 <el-input placeholder="请输入内容" v-model="input" :disabled="true"></el-input>
          </div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">删除框<el-input
           placeholder="请输入内容"
           v-model="input"
           clearable>
          </el-input></div></el-col>
          <el-col :span="8"> <div class="grid-content">密码框<el-input placeholder="请输入密码" v-model="input" show-password></el-input></div></el-col>
        </el-row>
      </div></div>
      <sTitle>
      <template #title>带 icon 的输入框</template>
      <template #introduce>带有图标标记输入类型</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row style="line-height: 50px" type="flex" justify="space-around">
          属性方式：
          <el-col :span="8"> <div class="grid-content">
<el-input
    placeholder="请选择日期"
    suffix-icon="el-icon-date"
    v-model="input1">
  </el-input></div></el-col>
          <el-col :span="8"> <div class="grid-content">
<el-input
    placeholder="请输入内容"
    prefix-icon="el-icon-search"
    v-model="input1">
  </el-input></div></el-col>
        </el-row>
        <el-row style="line-height: 50px" type="flex" justify="space-around">
          slot 方式：
          <el-col :span="8"> <div class="grid-content">
  <el-input
    placeholder="请选择日期"
    v-model="input1">
    <i slot="suffix" class="el-input__icon el-icon-date"></i>
  </el-input></div></el-col>
          <el-col :span="8"> <div class="grid-content">
    <el-input
    placeholder="请输入内容"
    v-model="input1">
    <i slot="prefix" class="el-input__icon el-icon-search"></i>
  </el-input></div></el-col>
        </el-row>
      </div></div>
      <sTitle>
      <template #title>文本域</template>
      <template #introduce>用于输入多行文本信息，通过将 type 属性的值指定为 textarea。</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
       <el-row>
       <div> <span>row控制显示行数-2</span></div>
  <el-input
  type="textarea"
  :rows="2"
  placeholder="请输入内容"
  v-model="textarea">
</el-input></el-row>
       <el-row>
        <div><span>autosize自适应显示行数</span></div>
        <el-input
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model="textarea">
</el-input>
       </el-row>
       <el-row>
        <div><span>autosize作为对象控制行数-2~4</span></div>
        <el-input
  type="textarea"
  :autosize="{ minRows: 2, maxRows: 4}"
  placeholder="请输入内容"
  v-model="textarea">
</el-input>
       </el-row>
      </div></div>
      <sTitle>
<template #title>复合型输入框</template>
      <template #introduce>可前置或后置元素，一般为标签或按钮</template>
</sTitle>
<div class="outbox">
<div class="inbox">
  <div class="row">
    <el-input  placeholder="请输入内容" v-model="input3">
    <template slot="prepend">Http://</template>
  </el-input>
</div>
<div class="row">
  <el-input  placeholder="请输入内容" v-model="input3">
    <template slot="append">.com</template>
  </el-input>
</div>
<div class="row">
  <el-input placeholder="请输入内容" v-model="input3" class="input-with-select">
    <el-select v-model="select" slot="prepend" placeholder="请选择">
      <el-option label="餐厅名" value="1"></el-option>
      <el-option label="订单号" value="2"></el-option>
      <el-option label="用户电话" value="3"></el-option>
    </el-select>
    <el-button slot="append" icon="el-icon-search"></el-button>
  </el-input>
</div>
</div>
</div>
<sTitle>
<template #title>带输入建议</template>
      <template #introduce>根据输入内容提供对应的输入建议</template>
</sTitle>
<div class="outbox">
<div class="inbox">
  <el-row class="demo-autocomplete">
  <el-col :span="12">
    <div class="sub-title">激活即列出输入建议</div>
    <el-autocomplete
      class="inline-input"
      v-model="state1"
      :fetch-suggestions="querySearch"
      placeholder="请输入内容"
      @select="handleSelect"
    ></el-autocomplete>
  </el-col>
  <el-col :span="12">
    <div class="sub-title">输入后匹配输入建议</div>
    <el-autocomplete
      class="inline-input"
      v-model="state2"
      :fetch-suggestions="querySearch"
      placeholder="请输入内容"
      :trigger-on-focus="false"
      @select="handleSelect"
    ></el-autocomplete>
  </el-col>
</el-row>
</div></div>
  </div>

</template>

<script>
// import request from '@/utils/request'
export default {
  name: 'ColorIndex',
  data () {
    return {
      input: '123',
      input1: '',
      input3: '',
      select: '',
      restaurants: [],
      state1: '',
      state2: '',
      textarea: '园有静观、动观之分，这一点我们在造园之先，首要考虑。何谓静观，就是园中予游者多驻足的观赏点;动观就是要有较长的游览线。二者说来，小园应以静观为主，动观为辅，庭院专主静观。大园则以动观为主，静观为辅。前者如苏州网师园，后者则苏州拙政园差可似之。人们进入网师园宜坐宜留之建筑多，绕池一周，有槛前细数游鱼，有亭中待月迎风，而轩外花影移墙，峰峦当窗，宛然如画，静中生趣。至于拙政园径缘池转，廊引人随，与“日午画船桥下过，衣香人影太匆匆”的瘦西湖相仿佛，妙在移步换影，这是动观。立意在先，文循意出。动静之分，有关园林性质与园林面积大小。象上海正在建造的盆景园，则宜以静观为主，即为一例。中国园林是由建筑、山水、花木等组合而成的一个综合艺术品，富有诗情画意。叠山理水要造成“虽由人作，宛自天开”的境界。山与水的关系究竟如何呢?简言之，模山范水，用局部之景而非缩小(网师园水池仿虎丘白莲池，极妙)，处理原则悉符画本。山贵有脉，水贵有源，脉源贯通，全园生动。我曾经用“水随山转，山因水活”与“溪水因山成曲折，山蹊随地作低平”来说明山水之间的关系，也就是从真山真水中所得到的启示。明末清初叠山家张南垣主张用平冈小陂、陵阜陂阪，也就是要使园林山水接近自然。如果我们能初步理解这个道理，就不至于离自然太远，多少能呈现水石交融的美妙境界。'
    }
  },
  methods: {
    querySearch (queryString, cb) {
      const restaurants = this.restaurants
      const results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter (queryString) {
      return (restaurant) => {
        return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    // async loadGoods () {
    //   const res = await request.get('/category/list')
    //   console.log(res)
    // },
    loadAll () {
      return [
        { value: '三全鲜食（北新泾店）', address: '长宁区新渔路144号' },
        { value: 'Hot honey 首尔炸鸡（仙霞路）', address: '上海市长宁区淞虹路661号' },
        { value: '新旺角茶餐厅', address: '上海市普陀区真北路988号创邑金沙谷6号楼113' },
        { value: '泷千家(天山西路店)', address: '天山西路438号' },
        { value: '胖仙女纸杯蛋糕（上海凌空店）', address: '上海市长宁区金钟路968号1幢18号楼一层商铺18-101' },
        { value: '贡茶', address: '上海市长宁区金钟路633号' },
        { value: '豪大大香鸡排超级奶爸', address: '上海市嘉定区曹安公路曹安路1685号' }]
    },
    handleSelect (item) {
      console.log(item)
    }
  },

  mounted () {
    this.restaurants = this.loadAll()
    // this.loadGoods()
  }
}

</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .row{
        width: 80%;
        position: relative;
        left: 10%;
        height: 80px;
        margin-top: 20px;

     .el-select{
      width: 100px;
      .el-input {
          width: 100px;
        }
        }
      }
      .el-row{
        span{
     color: #909399;
        }
      }
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;
        .el-input{
          zoom: 140%
}}
    }

}

</style>
